import React from 'react';
import cn from 'classnames';
import { parseISO } from 'date-fns';
import { DaysListDayProps } from 'shared/types/calendar';

import styles from './styles.scss';

const Day: React.FC<DaysListDayProps> = ({
  date,
  day,
  isDisabled,
  isInRange,
  isSelected,
  isToday,
  onClick,
  rowHeight
}) => {
  const handleClick = (): void => {
    if (!isDisabled && onClick) {
      onClick(parseISO(date));
    }
  };

  return (
    <li
      style={ { height: rowHeight } }
      className={ cn(styles.day, {
        [styles['day--today']]: isToday,
        [styles['day--selected']]: isSelected,
        [styles['day--disabled']]: isDisabled,
        [styles['day--inRange']]: isInRange
      }) }
      onClick={ handleClick }
      role="presentation"
    >
      { day }
    </li>
  );
};

export default Day;
